import { useMemo, useState, useEffect } from 'react';
import { FormProvider, useForm } from 'react-hook-form';
import { Link } from "react-router-dom"
import { toast } from 'react-toastify';
import { Icon } from '@iconify/react';

import { API_CATEGORY, API_EVENT, API_LOCATION } from '../../config';
import axios from '../../utils/axios';
import useLocales from '../../hooks/useLocales';

import dayjs from 'dayjs';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { MobileDateTimePicker } from '@mui/x-date-pickers/MobileDateTimePicker';

import Page from '../../components/Page';
import PageBanner from '../../components/wrappers/PageBanner';
import { FloatingInput } from '../../components/form/FloatingInput';
import GradientBorderWrapper from '../../components/wrappers/GradientBorderWrapper';

export default function AddEvent() {
    const { t } = useLocales();
    const [sending, setSending] = useState(false);
    const [dateTime, setDateTime] = useState(dayjs());
    const [locations, setLocations] = useState([]);
    const [categories, setCategories] = useState([]);

    const [selectedLocation, setSelectedLocation] = useState("");
    const [selectedCategory, setSelectedCategory] = useState("");

    const { register, handleSubmit, setValue, watch } = useForm({
        defaultValues: useMemo(() => {
            return {
                name: '',
                place: '',
                category: '',
                location: '',
                dateTime: dayjs(),
                facebookUrl: '',
                containsTime: true
            };
        }, [])
    });

    const category = watch("category");
    const location = watch("location");

    const onChangeDateTime = (value) => {
        setDateTime(value);
        setValue('dateTime', value);
    };
    const onSubmit = async (data) => {
        setSending(true);
        const result = await axios.post(API_EVENT.addNewEvent, data);
        if (result.status === 200) {
            toast.success(result.data?.message);
        } else {
            toast.error(result.data?.message);
        }
        setSending(false);
    };

    const onChangeCategory = (val) => {
        const selectedItem = categories.filter((category) => category._id === val)[0];
        setSelectedCategory(selectedItem.name);
        setValue("category", val)
    }

    const onChangeLocation = (val) => {
        const selectedItem = locations.filter((location) => location._id === val)[0];
        setSelectedLocation(`${selectedItem.city.name} • ${selectedItem.city.stateCode} • ${selectedItem.city.countryCode}`);
        setValue("location", val)
    }

    const getLocation = async () => {
        setSending(true);
        await axios
            .get(`${API_LOCATION.root}`)
            .then(({ data }) => {
                setLocations(data.data);
                setSending(false);
            })
            .catch((res) => {
                setSending(false);
            });
    }

    const getCategory = async () => {
        setSending(true);
        await axios
            .get(`${API_CATEGORY.root}`)
            .then(({ data }) => {
                setCategories(data.data);
                setSending(false);
            })
            .catch((res) => {
                setSending(false);
            });
    }

    useEffect(() => {
        getLocation();
        getCategory();
    }, []);

    return (
        <Page title="Add Event">
            <PageBanner>
                <div className="container mb-8">
                    <div className="flex justify-center  px-2">
                        <FormProvider>
                            <form onSubmit={handleSubmit(onSubmit)}>
                                <div className="flex flex-col gap-4  p-2 max-w-4xl">
                                    <h3 className="text-4xl mb-4">{t('title.add_new_event')}</h3>
                                    <div className="alert rounded-xl bg-yellow-400/60 flex gap-2 justify-start items-center mb-4">
                                        <Icon icon="mi:warning" color="text-yellow-400" width={24} />
                                        <p>{t('description.new_event_alert')}</p>
                                    </div>
                                    <div className="grid grid-cols-12 gap-4">
                                        <div className="col-span-12 md:col-span-6">
                                            <div className="relative w-full min-w-[200px] h-14 mt-4 mb-2">
                                                <FloatingInput
                                                    register={register('name')}
                                                    label={t('title.new_event_name')}
                                                />
                                            </div>
                                            <span className="text-stone-400 text-sm">
                                                {t('description.new_event_name_desc')}
                                            </span>
                                        </div>

                                        <div className="col-span-12 md:col-span-6">
                                            <div className="relative w-full min-w-[200px] h-14 mt-4">
                                                <FloatingInput
                                                    register={register('place')}
                                                    label={t('title.new_event_place')}
                                                />
                                            </div>
                                        </div>

                                        <div className="col-span-12 md:col-span-6">
                                            <div className="dropdown w-full">
                                                <GradientBorderWrapper tabIndex={0} >
                                                    <FloatingInput
                                                        register={register('category')}
                                                        value={selectedCategory}
                                                        label={t('title.category')}
                                                    />
                                                </GradientBorderWrapper>
                                                <ul className="dropdown-content menu shadow bg-base-200 rounded-xl w-full">
                                                    <GradientBorderWrapper>
                                                        <li className="p-4 border-b border-stone-600">
                                                            <p className="text-base text-primary p-0 hover:bg-inherit">{t("title.category")}</p>
                                                        </li>
                                                        {categories.map((option, i) => (
                                                            <li key={i}>
                                                                <Link to="#" className={`p-4 flex flex-row justify-between border-stone-600 ${category === option._id && "active"} ${i !== categories.length - 1 && "border-b"}`} onClick={() => onChangeCategory(option._id)}  >
                                                                    {option.name}
                                                                </Link>
                                                            </li>
                                                        ))}
                                                    </GradientBorderWrapper>
                                                </ul>
                                            </div>
                                        </div>

                                        <div className="col-span-12 md:col-span-6">
                                            <div className="dropdown w-full">
                                                <GradientBorderWrapper tabIndex={0} >
                                                    <FloatingInput
                                                        register={register('location')}
                                                        value={selectedLocation}
                                                        label={t('title.location')}
                                                    />
                                                </GradientBorderWrapper>
                                                <ul className="dropdown-content menu shadow bg-base-200 rounded-xl w-full">
                                                    <GradientBorderWrapper>
                                                        <li className="p-4 border-b border-stone-600">
                                                            <p className="text-base text-primary p-0 hover:bg-inherit">{t("title.location")}</p>
                                                        </li>
                                                        {locations.map((option, i) => (
                                                            <li key={i}>
                                                                <Link to="#" className={`p-4 flex flex-row justify-between border-stone-600 ${location === option._id && "active"} ${i !== locations.length - 1 && "border-b"}`} onClick={() => onChangeLocation(option._id)} >
                                                                    {`${option.city.name} • ${option.city.stateCode} • ${option.city.countryCode}`}
                                                                </Link>
                                                            </li>
                                                        ))}
                                                    </GradientBorderWrapper>
                                                </ul>
                                            </div>
                                        </div>

                                        <div className="col-span-12 md:col-span-6">
                                            <LocalizationProvider dateAdapter={AdapterDayjs}>
                                                <h6 className="mb-2 ml-3">{t('title.new_event_time')}</h6>
                                                <MobileDateTimePicker
                                                    disablePast
                                                    className="date-time-picker w-full"
                                                    value={dateTime}
                                                    onChange={onChangeDateTime}
                                                />
                                            </LocalizationProvider>
                                            <div className="w-full flex">
                                                <label className="cursor-pointer label">
                                                    <span className="label-text text-stone-400">
                                                        {t('title.new_event_set_time')}&nbsp;
                                                    </span>
                                                    <input
                                                        type="checkbox"
                                                        className="toggle toggle-primary"
                                                        {...register('containsTime')}
                                                    />
                                                </label>
                                            </div>
                                        </div>
                                        <div className="col-span-12 md:col-span-6 mt-4">
                                            <div className="relative w-full min-w-[200px] h-14 mt-4 mb-2">
                                                <FloatingInput
                                                    register={register('facebookUrl')}
                                                    label={t('title.new_event_link')}
                                                />
                                                <span className="text-stone-400 text-sm">
                                                    {t('description.new_event_link_desc')}
                                                </span>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="w-full justify-end flex">
                                        <button
                                            type="submit"
                                            className={`${sending ? 'loading' : ''} btn btn-primary capitalize px-10`}
                                        >
                                            {t('action.send')}
                                        </button>
                                    </div>
                                </div>
                            </form>
                        </FormProvider>
                    </div>
                </div>
            </PageBanner>
        </Page>
    );
}
